import React from "react"
import { Link, Switch, Route, useParams } from "react-router-dom"
import Flashcardlist from "../Flashcard/Flashcardlist.js"
import Topics from "./Topics.js"
import SideNav from "./SideNav.js"
// import Home from "./Home.js"
// import styled from "styled-components"
// import Flip from "./Flip.js"
import Btnchangelesson from "./Btnchangelesson.js"
import Book2list from "../Flashcard/book2list.js"
import Book3list from "../Flashcard/book3list.js"
import Home from "./Home.js"

export default function Topnav() {
  return (
    <>
      <nav className="topnav">
        {/* <Link className="navitem" to="/">
          Home
        </Link> */}
        <Link className="navitem" to="/Medina-arabic">
          Medina Arabic
        </Link>
        <Link className="navitem" to="/quiz">
          Quiz
        </Link>
        {/* <Link className="navitem" to="/topics">
          Topics
        </Link> */}
      </nav>

      <Switch>
        <Route exact path="/">
          <Home />
        </Route>
        <Route exact path="/Medina-arabic">
          <MedinaArabic />
        </Route>
        <Route path="/topics">
          <Topics />
        </Route>
        <Route path="/quiz">
          <Topics />
        </Route>
        <Route path="/Medina-arabic/book1/:id">
          <Child book={1} />
        </Route>
        <Route path="/Medina-arabic/book2/:id">
          <Child book={2} />
          {/* <Child2 /> */}
        </Route>
        <Route path="/Medina-arabic/book3/:id">
          {/* <Child book={3} /> */}
          <Child3 />
        </Route>
      </Switch>
    </>
  )
}

function MedinaArabic() {
  return (
    <>
      <Switch></Switch>
      <main>
        <SideNav page="main" />
        <div className="content">
          <h2>MedinaArabic</h2>
          <Btnchangelesson />
        </div>
      </main>
    </>
  )
}

function Child(props) {
  let { id } = useParams()
  let intid = parseInt(id)
  return (
    <main className="">
      <SideNav />
      <div className="content">
        <h1>Book {props.book}</h1>
        <Btnchangelesson />
        <Flashcardlist id={intid} urlid={true} book={props.book} />
      </div>
    </main>
  )
}

function Child2() {
  let { id } = useParams()
  let intid = parseInt(id)
  return (
    <main className="">
      <SideNav />
      <div className="content">
        <h1>Book 2</h1>
        <Btnchangelesson />
        <Book2list id={intid} urlid={true} />
      </div>
    </main>
  )
}

function Child3() {
  let { id } = useParams()
  let intid = parseInt(id)
  return (
    <main className="">
      <SideNav />
      <div className="content">
        <h1>Book 3</h1>
        <Btnchangelesson />
        <Book3list id={intid} urlid={true} />
      </div>
    </main>
  )
}
