import Flashcard from "./Flashcard"
import book1 from "../data/book1.json"
import book2 from "../data/book2.json"
// import book3 from "../data/book3.json"
import React, { useState, useEffect } from "react"

export default function Flashcardlist(props) {
  console.log("BOOKLISTALL")
  let [myid, setmyid] = useState(1)
  let book = props.book === 1 ? book1 : props.book === 2 ? book2 : book1

  console.log(props)
  console.log("book: " + book.filter((obj) => obj.L === myid))
  if (props.urlid === true && myid !== props.id) {
    setmyid(props.id)
  }

  // if (props.book === 1) {
  // } else if (props.book === 2) {
  // } else {
  // }
  const [flashcards, setflashcards] = useState(SampleFlashcard)
  useEffect(() => {
    let filteredArray = book.filter((obj) => obj.L === myid)

    setflashcards(
      filteredArray.map((questionItem, index) => {
        const answer = questionItem.En
        // multiple choice options
        // const option = [questionItem.Arabic, questionItem.English]
        return {
          id: `${index}`,
          question: questionItem.Ar,
          answer: answer,
          options: [],
        }
      })
    )
  }, [myid])

  // console.log(props.id)
  // console.log(filteredArray)
  return (
    <>
      {/* <button onClick={PreviousLesson}>Previous</button>
      <button onClick={NextLesson}>Next</button> */}
      <h1>
        Lesson {myid} / {props.id}
        {/* Lesson {myid} */}
      </h1>
      {/* <Testing id={myid} /> */}
      <div className="card-grid">
        {flashcards.map((flashcard) => {
          console.log(flashcard)
          return (
            <Flashcard
              flashcard={flashcard}
              book={"bookNumber"}
              key={flashcard.id}
            />
          )
        })}
      </div>
    </>
  )
}

const SampleFlashcard = [
  {
    id: 1,
    question: "what is 2 + 3",
    answer: "4",
    options: ["2", "3", "4", "5"],
  },
  {
    id: 2,
    question: "question 2",
    answer: "answer",
    options: ["answer", "3", "4", "5"],
  },
]
